
export default {
  data(){
    return{
    }
  },
  methods: {
    toPage(page) {
      uni.navigateTo({
        url: page,
      });
    },
    toHome() {
      uni.switchTab({
        url: '/pages/index/index',
      });
    },
    toSearch() {
      uni.navigateTo({
        url: '/pages/goods/search',
      });
    },
    toGoods(id) {
      uni.navigateTo({
        url: '/pages/goods/detail?id=' + id,
      });
    },
    toShop(id) {
      uni.navigateTo({
        url: '/pages/shop/index?shopid=' + id,
      });
    },
    back() {
      uni.navigateBack();
    },
  },

};
